import { Tab, Tabs, ThemeProvider, createTheme } from '@mui/material'
import Character from './Character'
import Dice from './Dice'
import Spacer from './Spacer'
import { useState } from 'react'
import Rules from './Rules'
import MagicRules from './MagicRules'

const theme = createTheme({
  typography: {
    fontFamily: [
      'Josefin Sans',
      '-apple-system',
      'BlinkMacSystemFont',
      'Segoe UI',
      'Roboto',
      'Oxygen',
      'Ubuntu',
      'Cantarell',
      'Fira Sans',
      'Droid Sans',
      'Helvetica Neue',
      'sans-serif',
    ].join(',')
  }
})

function App() {
  const [tab, setTab] = useState(0)

  return <ThemeProvider theme={theme}>
    <div style={{ padding: 20, height: '100%', background: '#E8E4D8', display: 'flex', flexDirection: 'column' }}>
      <div>
      <Tabs value={tab} onChange={(ev, newTab) => setTab(newTab)} >
        <Tab label="Character" />
        <Tab label="Rules (Basic)" />
        <Tab label="Rules (Magick)" />
      </Tabs>
      </div>
      { tab === 0 && <Character /> }
      { tab === 1 && <Rules onJumpToMagick={() => setTab(2)} /> }
      { tab === 2 && <MagicRules /> }
    </div>
  </ThemeProvider>
}

export default App
