import React, { useEffect, useState } from 'react'

import { Stack, TextField } from '@mui/material'
import Ancestry from './Ancestry'
import Inclinations from './Inclinations'
import HeroPath from './HeroPath'
import Health from './Health'
import Karma from './Karma'
import HeroDie from './HeroDie'
import Spacer from './Spacer'
import Boons from './Boons'
import Roller from './Roller'
import CharacterFeatures from './CharacterFeatures'
import Gear from './Gear'
import MiraculousSave from './MiraculousSave'
import Armor from './Armor'
import { LoadCharacterField, SaveCharacterField } from './CharacterStore'

// To add:
// Persistence

export default function Character() {
  const [name, setName] = useState('')
  const [heroPath, setHeroPath] = useState('')
  const [subclass, setSubclass] = useState('')
  const [ancestry, setAncestry] = useState(null)
  const [inclinations, setInclinations] = useState([])
  const [boons, setBoons] = useState([])

  const handleName = (ev) => {
    setName(ev.target.value)
    SaveCharacterField('name', ev.target.value)
  }

  const handleAncestry = (ancestry) => {
    setAncestry(ancestry)
    SaveCharacterField('ancestry', ancestry)
  }

  const handleHeroPath = (hp, sc) => {
    setHeroPath(hp)
    setSubclass(sc)

    SaveCharacterField('heroPath', ancestry)
    SaveCharacterField('subclass', sc)
  }

  const handleInclinations = (inclinations) => {
    setInclinations(inclinations)
    SaveCharacterField('inclinations', inclinations)
  }

  // This should run once when the compnent finishes mounted
  useEffect(() => {
    const loadData = async () => {
      const name = await LoadCharacterField('name')
      const ancestry = await LoadCharacterField('ancestry')
      const heroPath = await LoadCharacterField('heroPath')
      const subclass = await LoadCharacterField('subclass')

      setName(name || '')
      setAncestry(ancestry || '')
      setHeroPath(heroPath || '')
      setSubclass(subclass || '')
    }

    loadData()
  }, [])

  return (
    <div style={{display: 'flex', marginTop: 10, flexDirection: 'column', width: 680 }}>
      <Stack direction="row">
        <TextField sx={{ flex: 5 }} label="Name" value={name} onChange={handleName} />
        <Spacer/>
        <Roller boons={boons} />
      </Stack>

      <Stack direction="row"
             justifyContent="space-between"
             style={{ marginTop: 20 }}
      >
        <Ancestry onChange={handleAncestry} />
        <Spacer />
        <HeroPath onChange={handleHeroPath} />
      </Stack>

      <Inclinations ancestry={ancestry} heroPath={heroPath} onChange={handleInclinations} />

      <Stack direction="row" style={{ display: 'flex', justifyContent: 'space-between', marginTop: 20 }}>
        <Health heroPath={heroPath} />
        <Karma />
        <HeroDie inclinations={inclinations} />
        <Armor heroPath={heroPath} />
        <MiraculousSave heroPath={heroPath} />
      </Stack>

      <Stack direction="row" style={{ display: 'flex', justifyContent: 'space-between', marginTop: 20 }}>
      </Stack>

      <CharacterFeatures ancestry={ancestry}
                         heroPath={heroPath}
                         subclass={subclass}
                         inclinations={inclinations}
      />

      <Boons ancestry={ancestry}
             heroPath={heroPath}
             inclinations={inclinations}
             onCalculate={(b) => setBoons(b)}
      />

      <Gear heroPath={heroPath} inclinations={inclinations} />
    </div>
  )
}
