export async function loadCharacter() {
  if (OBR.isReady) {
    const player = OBR.player
    const metadata = await player.getMetadata()
    metadata['com.snak.obr_ezd6'] = { latest: new Date().getTime() }
    await player.setMetadata(metadata)
  }
  // return {
  //   name: localStorage.getItem('name'),
  //   ancestry: localStorage.getItem('ancestry'),
  //   heroPath: localStorage.getItem('heroPath'),
  //   subclass: localStorage.getItem('subclass')
  // }
}

export async function LoadCharacterField(field) {
  if (OBR.isReady) {
    const player = OBR.player
    const metadata = await player.getMetadata()
    const character = metadata['com.snak.obr_ezd6/character']

    console.log('Loaded field ', { [field]: character[field] })

    return character[field]
  }

  console.log('Loaded field ', { [field]: null })

  return ''
}

export async function SaveCharacterField(field, value) {
  console.log('Saving field ', { [field]: value })
  if (OBR.isReady) {
    const player = OBR.player
    const metadata = await player.getMetadata()
    metadata['com.snak.obr_ezd6/character'] = { [field]: value }
    await player.setMetadata(metadata)
  }
}
